* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  /*-webkit-text-size-adjust: none;*/
  /*-webkit-user-select: none;*/
  /*-ms-user-select: none;*/
  /*user-select: none;*/
  font-family: Arial, "微软雅黑",sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,html>body,#root{
  background-color:#f5f5f5;
  height:100%;
  /*width:100%;*/
}

img {
  border: none;
  vertical-align: middle;
}

body, p, form, input, button, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*h1, h2, h3, h4, h5, h6 {*/
  /*font-size: 100%;*/
/*}*/

input, textarea {
  -webkit-user-select: text;
  -ms-user-select: text;
  -moz-user-select: text;
       user-select: text;
  -webkit-appearance: none;
  font-size: 1em;
  line-height: 1.5em;
}

table {
  border-collapse: collapse;
}

input, select, textarea {
  outline: none;
  border: none;
  background: none;
}

a {
  outline: 0;
  cursor: pointer;
  *star: expression(this.onbanner=this.blur());
}

a:link, a:active {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  color:#999;
}

a {
  text-decoration: none;
  -webkit-touch-callout: none;
}

em, i {
  font-style: normal;
}

li, ol {
  list-style: none;
}

.clear {
  clear: both;
  height: 0;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
  overflow: hidden;
}

/*.fl {*/
  /*float: left;*/
/*}*/

/*.fr {*/
  /*float: right;*/
/*}*/


.flex-row-between {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.flex-row-around {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.flex-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.no_data{
  text-align: center;
  font-size: .26rem;
  padding:1rem 0 3rem;
}
.not_more{
  text-align: center;
  font-size: .26rem;
  padding:.3rem 0 ;
}
.bla_mask{
  position: fixed;
  width: 100%;
  height:100%;
  background-color: rgba(0,0,0,.7);
}

.cornermark{
  position: absolute;
  top:0;
  left:0;
  width:.71rem !important;
  height:.65rem !important;
}
.po_relative{
  position: relative;
}
.bot_height{
  width:100%;
  height:1.1rem;
}
.b_height{
  width:100%;
  height:1rem;
}


