.exchange_header{
    height:2.9rem;
    width:100%;
    background-color: #f69814;
    -ms-flex-pack: center;
        justify-content: center;
    color: #fff;
}
.exchange_header .tab{
    height:.5rem;
    width:2.65rem;
    background-color: #f69814;
    font-size: .28rem;
    /*border-radius: .18rem;*/

}
.exchange_header .tab_item{
    width:1.325rem;
    height:.5rem;
    line-height:.5rem;
    text-align: center;
    border: .02rem solid #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.exchange_header .tab_item_act{
    background-color: #fff;
    color: #f69814;
}
.deal_exchange .ex_rules{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height:1.12rem;
    -ms-flex-align: start;
        align-items: flex-start;
    padding:.25rem .3rem;
}
.deal_exchange .ex_rules .ex_rules_left1{
    font-size: .24rem;
    color: #acacac;
    line-height: .3rem;
}
.deal_exchange .ex_rules .ex_rules_right{
    font-size: .28rem;
    color: #81abcb;
}
.deal_exchange .commit_btn{
    background: url("./img/btn@2x.png");
    background-size: 100% 100%;
    width:7.1rem;
    height:.89rem;
    line-height:.89rem;
    color: #fff;
    text-align: center;
    font-size: .34rem;
    margin:.7rem auto;
}
.modal_content_1{
    display: inline-block;
    text-align: justify;
    text-align-last: justify;
}


