.zz_header_cont{
    -ms-flex-pack: center;
        justify-content: center;
    width:100%;
    height:2.52rem;
    background-color: #f69814;
    color: #fff;
}
.zz_header_cont img{
    width:.91rem;
    height:.84rem;
}
.zz_header_cont .oec_num{
    font-size: .6rem;
    padding:.1rem 0;
}
.zz_header_cont .oec_change{
    font-size: .4rem;
}

.zz_header_cont1 .mine_score {
    width: 100%;
    height: 1rem;
    background-color: #fff;
}

.zz_header_cont1 .mine_score_li {
    width: 50%;
    height: 1rem;
    position: relative;
    -ms-flex-pack: center;
        justify-content: center;
}

.zz_header_cont1 .mine_score_li_r {
    margin-left: .35rem;
}
@media screen and (max-width: 320px) {
    .zz_header_cont1 .mine_score_li_r {
        margin-left: .15rem;
    }
}
.zz_header_cont1 .mine_score_li_l img {
    width: .44rem;
    height: .44rem;
}

.zz_header_cont1 .mine_score_li_l span {
    color: #181818;
    font-size: .18rem;
    margin-top: .1rem;
}

.zz_header_cont1 .mine_score_li_r span {
    color: #a8a8a8;
    font-size: .17rem;
    margin-top: .1rem;
}

.zz_header_cont1 .line_img {
    width: .02rem;
    height: .82rem;
    position: absolute;
    right: 0;
    top: .09rem;
}

.zz_header_cont1 .mine_score .mine_score1 {
    font-size: .28rem;
    color: #0fc948;
}

.zz_header_cont1 .mine_score .mine_score2 {
    font-size: .28rem;
    color: #ef7a00;
}
.zz_header_cont1 .ba_img{
    width:7.4rem;
    height:2rem;
    margin: .2rem .05rem;
}


