.ex_pwd{
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    z-index: 99;
}
.ex_pwd .pwd_cont{
    width: 100%;
    background-color: #fff;
    height:5.28rem;
    position: absolute;
    bottom: 0;
    z-index: 100;
    -ms-flex-align: start;
        align-items: flex-start;
}
.ex_pwd .pwd_header{
    width: 6.9rem;
    height:.8rem;
    margin:.2rem .3rem 0;
    font-size: .3rem;
    border-bottom: 1px solid #bfbfbf;
    line-height: .8rem;
    text-align: center;
    color: #303030;
}

.ex_pwd .red{
    color: #fd860a;
}
.ex_pwd .pwd_tip_cont{
    width:100%;
}
.ex_pwd .tip_text{
    width:6.9rem;
    margin:.6rem auto;
    color: #303030;
    font-size: .28rem;
    text-align: center;
}
.ex_pwd .pwd_btn{
    width: 100%;
    padding:0 .3rem;
}
.ex_pwd .pwd_btn input{
    color: #646464;
    width: 4.3rem;
    background: #dedede;
    height:.85rem;
    text-align: center;
    border-radius: .26rem;
}

.ex_pwd .pwd_btn div{
    width:1.5rem;
    height:.85rem;
    background-color: #ff911e;
    color: #fff;
    line-height: .85rem;
    text-align: center;
    border-radius: .24rem;
    margin-left:.5rem;
}


