.home_header{
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    background-color: #ff8608;
    width: 100%;
    height: 2.1rem;
    position:relative;
}
.home_header .header{
    font-size: .3rem;
    text-align: center;
    color: white
}
.home_header .user{
    -ms-flex-align:start;
        align-items:flex-start;
    margin-left:.45rem
}
.home_header .scoreImg{
    width:.60rem;
    height:.60rem;
}
.home_header .score{
    -ms-flex-align:center;
        align-items:center;
    margin-left:.60rem;
}
.home_header .userPhoto{
    width: 1.30rem;
    height: 1.30rem;
    border-radius: 50% ;
    border: .06rem solid #ffb66b;
    margin-left: .27rem ;
}
.home_header .name{
    font-size: .30rem;
    text-align: center;
    color: white
}
.home_header .mobile{
    font-size: .22rem;
    text-align: center;
    color: white;
    margin-top: .30rem;
}
.home_header .scoreText{
    font-size: .22rem;
    text-align: center;
    color: #fde351;
    margin-top: .15rem;
}
.home_header .headerRight{
    position:absolute;
    top:.30rem;
    right:.20rem;
    width:.5rem;
    height:.5rem;
}
.home_header .message{
    position:absolute;
    left: .05rem;
    top: .05rem;
    width:.35rem;
    height:.35rem;
}
.home_header .messageNum{
    position:absolute;
    right: 0;
    width:.16rem;
    height:.16rem;
    line-height:.16rem;
    border-radius: 50% ;
    background-color: #f6200b;
    text-align: center;
    font-size:.12rem;
    color: #fff;
}
.home_header .messageNumText{

}


