.transfer_money .cash_withdrawals_form{
    margin-top: 0;
}
.cash_withdrawals_form .rmb{
    width:.27rem;
    height:.38rem;
}
.cash_withdrawals_form .am-list-header{
    background-color: #fff;
}
.cash_withdrawals_form .am-list-body{
    border-top: 0 none;
}
@media (-webkit-min-device-pixel-ratio: 2), not all, (-o-min-device-pixel-ratio: 2/1), (min-resolution: 2dppx){
    html:not([data-scale]) .cash_withdrawals_form  .am-list-body::before{
        height:0;
    }
}
.cash_withdrawals_form .total_cash{
    width: 100%;
    padding-left:.3rem;
    height:.6rem;
    line-height:.6rem;
    background-color: #fff;
}
.cash_withdrawals_form .am-input-label.am-input-label-5{
    width:.57rem;
}


