.withdrawals_record{
    width: 100%;
}
.withdrawals_record .list{
    width:100%;
}
.withdrawals_record .tip{
    font-size: .24rem;
    color: #fff;
    width:100%;
    height:.6rem;
    line-height:.6rem;
    text-align: center;
    background-color: #f9b46d;
}
.withdrawals_record .list_item{
    width:100%;
    background-color: #fff;
    margin-bottom: .2rem;
    height: 1.1rem;
    -ms-flex-pack: center;
        justify-content: center;
}
.withdrawals_record .item{
    width:100%;
    background-color: #fff;
    padding: 0 .3rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.withdrawals_record .item1{
    width:100%;
    background-color: #fff;
    padding: 0 .3rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: .08rem;
}
.withdrawals_record .item_text1{
    font-size: .3rem;
    color: #4c4c4c;
}

.withdrawals_record .item_text2{
    font-size: .3rem;
    color: #ff890b;
    font-weight: 600;
}
.withdrawals_record .item_text3{
    font-size: .24rem;
    color: #a8a8a8;
}

.withdrawals_record .item_text4{
    font-size: .26rem;
    color: #f9b46d;
}
/*提现失败*/
.withdrawals_record div.item_text4.act1{
    color: #fb6773;
}

/*提现成功*/
.withdrawals_record div.item_text4.act2{
    color: #2aa703;
}





