.login_cont{
    width:100%;
    height:100%;
    background: url("../img/loginbg@2x.png") no-repeat;
    background-size: 100% 100%;
}
.login_cont .container{
    width:100%;
    height:100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}


.to_login{
    padding-top: .2rem;
    width: 80%;
    text-align: right;
    font-size: .28rem;
    height: .75rem;
    line-height: .75rem;
    margin: 0 auto;
}

.logoImg{
    display: block;
    width: 5.04rem;
    height: 1.26rem;
    margin-bottom: 1rem;
}

.inputCont input{
    height: 1rem;
    line-height: 1rem;
}

.inputCont{
    font-size: .3rem;
    border-radius: .1rem;
    width: 6.5rem;
    height: 1rem;
    margin: .25rem auto;
    background-color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:start;
        justify-content:flex-start;
    position: relative;
}

.code_item{
    width: 6.5rem;
    height: 1rem;
    margin: .25rem auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:justify;
        justify-content:space-between;
    -ms-flex-align: center;
        align-items: center;
    border-radius: .1rem;
    background-color: #fff;
    font-size: .3rem;
}

.code_item .wx_userInput{
    font-size: .28rem;
    height: 1rem;
    line-height: 1rem;
    width: 3.46rem;
    margin-left:.3rem;
}

.code_item .img_code{
    width: 2.3rem;
    height:.8rem;
    padding-right: .2rem;
}

.inputCont .CodeTip{
    font-size: .28rem;
    color:#d2d2d2;
    line-height: .75rem;
    text-indent: .12rem;
    display: inline-block;
    padding-right:.48rem;
}
.inputCont span{
    font-size: .22rem;
    line-height: 1rem;
    color:#d2d2d2;
    position: absolute;
    right:.15rem;
}
.inputCont .sendCode{
    font-size: .28rem;
    height:.8rem;
    line-height: .8rem;
    padding: 0 .5rem;
    color: #fff;
    background-color: #f6c94e;
    border-radius: .1rem;
    margin-top: .1rem;
}
.userImg{
    margin:.115rem .8rem .115rem .12rem;
    width:.52rem;
    height:.52rem;
}
.userInput{
    outline:none;
    border:0 none;
    margin-left: .3rem;
}
input::-webkit-input-placeholder { /* WebKit browsers */
    color:#d2d2d2;
    font-size: .28rem;
}

.register{
    font-size: .3rem;
    width: 6.5rem;
    background-color: #fd900e;
    border-radius: .1rem;
    height:.9rem;
    line-height:.9rem;
    text-align: center;
    color:white;
    margin:.25rem 0 .25rem 0;
}
.reg_agree{
    width: 80%;
    margin: 0 auto;
    margin-top: 1rem;
}
.reg_agree a {
    color: #06f!important;
}
